@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@400;700&family=Poppins:wght@400;600;700&display=swap);
body{
  margin:0;
  padding:0;
  height:100vh
}

#root{
  height:100%
}

#run-btn{
  background:#b3b3b3;
  color:#00000090;
  box-shadow:6px 6px 8px 0 rgba(0,0,0,0.541),-2px -2px 12px 0 rgba(255,255,255,0.3);
  font-weight:bold
}

#run-btn:focus{
  outline:none
}

#run-btn:active{
  box-shadow:none
}

#run-btn svg{
  stroke-width:4px
}

#run-btn.loading{
  background:none;
  color:#ffffff90;
  box-shadow:none;
  pointer-events:none
}

#run-btn.loading svg{
  animation-name:spin;
  animation-duration:2000ms;
  animation-iteration-count:infinite;
  animation-timing-function:linear
}

@keyframes spin{
  from{
    transform:rotate(0deg)
  }

  to{
    transform:rotate(360deg)
  }
}



